import React, { useState, useEffect } from 'react';
import { supabase } from '../utilities/supabaseClient';
import { handleUpdateSelectedRowsFromSupabase } from '../utilities/updateSelectedRowsFromSupabase';
import { toastMessageDialog } from '../utilities/toastMessageDialog';
import { ToastType } from '../utilities/type';

const SendToSupabaseButton: React.FC = () => {
  const [toastMessage, setToastMessage] = useState<{ message: string; type: ToastType } | null>(null);

  useEffect(() => {
    if (toastMessage) {
      toastMessageDialog(toastMessage.type, toastMessage.message);
      setToastMessage(null);
    }
  }, [toastMessage]);

  const handleSendToSupabaseButton = async () => {
    try {
      await Excel.run(async (context) => {
        const sheet = context.workbook.worksheets.getActiveWorksheet();

        // Get all selected ranges (can be multiple areas)
        const selectedRanges = context.workbook.getSelectedRanges();
        selectedRanges.load("areas");
        await context.sync();

        if (selectedRanges.areas.items.length === 0) {
          console.log("Please select one or more rows first.");

          setToastMessage({
            type: "warning",
            message: "Please select one or more rows first."
          });
          return;
        }

        // Load used range to get total columns and headers
        const usedRange = sheet.getUsedRange();
        usedRange.load("columnCount, values");
        await context.sync();

        const columnCount = usedRange.columnCount;
        const headers: string[] = usedRange.values[0].map((h: any) =>
          (h ?? "").toString().trim()
        );

        const allRowsData: Record<string, any>[] = [];

        for (const area of selectedRanges.areas.items) {
          area.load(["rowIndex", "rowCount"]);
          await context.sync();

          for (let i = 0; i < area.rowCount; i++) {
            const rowIndex = area.rowIndex + i;

            // Get full row using getRangeByIndexes
            const rowRange = sheet.getRangeByIndexes(rowIndex, 0, 1, columnCount);
            rowRange.load("values");
            await context.sync();

            const rowValues = rowRange.values[0];
            const rowObject: Record<string, any> = {};

            for (let j = 0; j < Math.min(headers.length, rowValues.length); j++) {
              rowObject[headers[j]] = rowValues[j] ?? null;
            }

            allRowsData.push(rowObject);
          }
        }

        console.log("Selected Full Rows Data:", allRowsData);

        // Send to Supabase
        const payload = allRowsData
          .filter(row =>
            row["Kaizen ID #"] !== null &&
            row["Kaizen ID #"] !== undefined &&
            row["Kaizen ID #"].toString().trim() !== ""
          )
          .map((row) => ({
            id: row["Kaizen ID #"],
            kaizen: row
          }));

        console.log("PAYLOAD SENT TO SUPABASE:", payload);

        if (payload.length === 0) {
          console.warn("No valid rows with Kaizen ID found. Skipping Supabase upsert.");

          setToastMessage({
            type: "warning",
            message: "No rows with Kaizen ID detected. Please select rows with valid Kaizen IDs before sending."
          });
          return;
        }

        const { data, error } = await supabase
          .from("kaizens")
          .upsert(payload, { onConflict: "id" });

        if (error) {
          console.error("Supabase error:", error);

          setToastMessage({
            type: "error",
            message: error.message
          });
        } else {
          console.log(`Success! ${payload.length} row(s) upserted to Supabase.`);
          setToastMessage({
            type: "success",
            message: `${payload.length} row(s) successfully upserted to Supabase.`
          });
        }
      });
    } catch (error) {
      console.error("Error: " + error);
      if (error instanceof OfficeExtension.Error) {
        console.log("Debug info: " + JSON.stringify(error.debugInfo));
      }
    }
  };

  const handleGetFromSupabaseButton = async () => {
    try {
      const { data, error } = await supabase.from("kaizens").select("*");
      if (error || !data || data.length === 0) return;

      const rows = data
        .map(r => r.kaizen)
        .filter(k => k && k["Kaizen ID #"]);

      if (rows.length === 0) return;

      // Update only selected rows in sheet
      await handleUpdateSelectedRowsFromSupabase(rows);
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <>
      <div className="flex flex-col justify-center items-center gap-4 mt-8 h-screen">
        <button
          className="py-2.5 px-6 rounded-lg text-sm font-medium text-white bg-[#0F783F] hover:bg-[#0d6735] transition-all duration-200 cursor-pointer"
          onClick={handleSendToSupabaseButton}
        >
          Send Selected Data to Supabase
        </button>

        <button
          className="py-2.5 px-6 rounded-lg text-sm font-medium text-white bg-[#0F783F] hover:bg-[#0d6735] transition-all duration-200 cursor-pointer"
          onClick={handleGetFromSupabaseButton}
        >
          Get Selected Data from Supabase
        </button>
      </div>
    </>
  );
};

export default SendToSupabaseButton;